        .video {
            position: relative;
            max-height: 70rem;
            overflow: hidden;
        }
        
        .video-fondo {
            max-width: 100%;
            background-position: center center;
        }
        
        .overlayp {
            position: absolute;
            width: 100%;
            height: 100%;
            /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+77,9e37f2+77,ffffff+100&0.79+0,0.5+100 */
            background: -moz-linear-gradient(top, rgba(137, 28, 30, 0.79) 0%, rgba(158, 55, 242, 0.57) 77%, rgba(255, 255, 255, 0.5) 100%);
            /* FF3.6-15 */
            background: -webkit-linear-gradient(top, rgba(137, 28, 30, 0.79) 0%, rgba(158, 55, 242, 0.57) 77%, rgba(255, 255, 255, 0.5) 100%);
            /* Chrome10-25,Safari5.1-6 */
            background: linear-gradient(to bottom, rgba(137, 28, 30, 0.79) 0%, rgba(158, 55, 242, 0.57) 77%, rgba(255, 255, 255, 0.5) 100%);
        }
        
        video {
            display: block;
        }
        
        .contenido-video {
            display: flex;
            height: 100%;
            align-items: center;
            position: relative;
            z-index: 1;
            h2 {
                color: $blanco;
                font-size: 4rem;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                @include tablet {
                    font-size: 7rem;
                }
            }
            span {
                font-size: 2rem;
                background-color: $rosa;
                padding: 1rem 2rem;
                margin-top: 1rem;
            }
        }