//los mixin sirven para reutilizar codigo SASS que se va a reutilizar en multiples oportunidades.
@mixin tablet {
    @media (min-width: 768px) {
        @content; //permite usar llaves en los mixin y darle los parametros que se requieran
    }
}

@mixin grid($columnas, $espaciado) {
    display: grid;
    grid-template-columns: repeat( $columnas, 1fr);
    gap: $espaciado;
}