.footer {
    position: relative;
    z-index: 3;
    background-color: $rosa;
    padding: $separacion / 4;
    text-align: center;
    p {
        margin: 0;
        color: $blanco;
    }
}

#myBtn {
    display: none;
    /* Hidden by default */
    position: fixed;
    /* Fixed/sticky position */
    bottom: 19px;
    /* Place the button at the bottom of the page */
    right: 30px;
    /* Place the button 30px from the right */
    z-index: 2;
    /* Make sure it does not overlap */
    border: none;
    /* Remove borders */
    outline: none;
    /* Remove outline */
    background-color: rgba(250, 85, 8, 0.979);
    /* Set a background color */
    color: white;
    /* Text color */
    cursor: pointer;
    /* Add a mouse pointer on hover */
    padding: 0.5rem;
    /* Some padding */
    border-radius: 10px;
    /* Rounded corners */
    img {
        height: 4rem;
        width: 4rem;
    }
}

#myBtn:hover {
    background-color: #555;
    /* Add a dark-grey background on hover */
}