.header {
    background-color: $verde;
    .contenido-header {
        padding: $separacion / 5 0;
        @include tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
    h1 {
        color: $blanco;
        margin-bottom: 1rem;
        font-size: 3rem;
        text-align: center;
        @include tablet {
            margin-bottom: 0;
            font-size: 4rem;
        }
    }
    &.fijo {
        position: fixed;
        top: 0;
        width: 100%;
        box-shadow: 0 .3rem .4rem #444;
        z-index: 2;
    }
}

.fijo+.video {
    margin-top: 8.75rem;
    @include tablet {
        margin-top: 6.6rem;
    }
}

.fijo~.lineup,
.fijo~.galeria {
    padding-top: 9.8rem;
    @include tablet {
        padding-top: 8.2rem;
    }
}

.navegacion-principal {
    display: flex;
    justify-content: center;
    gap: 2rem;
    a {
        text-transform: uppercase;
        color: $blanco;
        font-size: 2rem;
        &:hover {
            //& te permite referenciar al elemento padre para un pseudoselector
            background-color: $amarillo;
        }
    }
}