.galeria {
    padding: $separacion 0;
    h3 {
        text-align: center;
    }
}

.galeria-imagenes {
    padding: 0;
    list-style: none;
    margin-top: $separacion;
    @include grid(2, $separacion);
    @include tablet {
        @include grid(6, $separacion / 2);
    }
}

.overlay {
    position: fixed;
    z-index: 3;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;
    & img {
        padding: 0;
        @include tablet {
            max-width: 80%;
            max-height: 80%;
        }
    }
}

.btn-cerrar {
    width: 4rem;
    height: 4rem;
    background-color: $blanco;
    display: grid;
    place-items: center;
    font-weight: $black;
    border-radius: 50%;
    &:hover {
        cursor: pointer;
    }
}